#projects {
    height: 100vh;
    background-color: rgb(0, 0, 0);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 60px; /* Offset for navbar */
    color: white;
    font-family: 'Kode Mono', monospace;
  }
  
  .project-container {
    display: flex;
    align-items: center;
    text-align: center;
  }
  
  .project-details {
    margin: 0 20px; /* Spacing between arrows and project details */
  }
  
  .project-image {
    width: 700px;
    height: auto;
    margin: 20px 0;
  }
  
  .nav-button {
    background: none;
    border: none;
    color: white;
    font-size: 24px;
    cursor: pointer;
    padding: 10px;
    transition: color 0.3s;
  }
  
  .nav-button:hover {
    color: #ccc;
  }
  
  /* Tambahkan ini di bagian CSS yang ada */

::-webkit-scrollbar {
    width: 8px; /* Lebar scrollbar */
  }
  
  ::-webkit-scrollbar-track {
    background: transparent; /* Warna latar belakang scrollbar */
  }
  
  ::-webkit-scrollbar-thumb {
    background: rgba(185, 0, 185, 0); /* Warna scroller */
    border-radius: 10px; /* Membuat sudut lebih halus */
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background: rgb(179, 0, 255); /* Warna saat hover */
  }
  
  /* Tambahkan ini jika Anda menggunakan scrollbar pada elemen tertentu */
  .project-container {
    overflow-y: auto; /* Pastikan container dapat discroll */
    max-height: 100vh; /* Batasi tinggi agar ada scroll */
  }
  
  .h1 {
    text-align: center;
  }