/* src/components/Home.css */
#home {
    height: 100vh;
    display: flex;
    justify-content: space-between; /* Align items to both sides */
    align-items: center;
    position: relative;
    overflow: hidden;
    padding-top: 60px;
    color: white;
}

.model-container {
    width: 50%; /* Limit the width of the model container */
    height: 100%; /* Ensure it takes up the full height */
}
  
.overlay {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    background-color: black;
    color: white;
    z-index: 1;
}
  
.text-container {
    text-align: left;
    font-family: 'Kode Mono', monospace;
    margin-right: auto;
    margin-left: 50px; /* Add space from the left */
    max-width: 800px; /* Set a max width for better readability */
}
  
h1 {
    font-size: 39px;
    font-weight: bold;
}
  
p {
    font-size: 17px;
    font-weight: medium;
    line-height: 1.15; /* Add line height for better readability */
}
