/* src/components/NavBar.css */
nav {
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #000000;
    z-index: 1000;
    box-shadow: 0 2px 4px rgba(141, 141, 141, 0.1);
}

ul {
    display: flex;
    justify-content: flex-start; /* Align items to the left */
    align-items: center;
    list-style-type: none;
    margin: 0;
    padding: 0;
    height: 75px;
    padding-left: 50px; /* Add space from the left */
}

li {
    margin: 0 20px;
}

a {
    text-decoration: none;
    color: white;
    font-weight: medium;
    font-size: 18px;
    transition: color 0.3s ease-in-out, text-shadow 0.3s ease-in-out;
    font-family: 'Kode Mono', monospace; /* Apply the Google Font */
}

a:hover {
    color: #882dff; /* Hover color */
    text-shadow: 0 0 32px #882dff98; /* Glow effect */
}

a.active {
    color: #6127ff; /* Active link color */
    text-shadow: 0 0 32px #6127ff9a; /* Glow effect */
}
